import lengthAccumulator from './length-accumulator'
import getProductByIndex from './get-product-by-index'

class FastCartesianProduct {
  constructor(sets) {
    this.sets = sets
  }

  get(index) {
    return getProductByIndex(this.sets, index)
  }

  [Symbol.iterator] () {
    const {length} = this
    const index = 0

    return {
      next() {
        const done = index >= length
        const value = this.get(index)

        index += 1

        return {
          value,
          done
        }
      }
    }
  }

  get array() {
    return Array.from(this)
  }

  get set() {
    return new Set(this)
  }

  get length() {
    return this.sets.reduce(lengthAccumulator, 1)
  }
}

export default FastCartesianProduct
